import SearchInput from "../../../components/SearchInput/SearchInput";

export default {
	data() {
		return {
			goodsList: [],
			GoodsInfonew: {}
		}
	},
	components: {
		SearchInput
	},
	onLoad: function(options) {
		uni.request({
			url: 'http://localhost:8080/commodity/selectCom',
			method: 'POST',
			data: {},
			success: res => {
				this.goodsList = res.data.message;
			}
		});
	},
	methods: {
		GW(e) {
			console.log(e);
			// 1 获取缓存中的购物车 数组
			let cart = uni.getStorageSync("cart") || [];
			// 2 判断 商品对象是否存在于购物车数组中
			let index = cart.findIndex(v => v.id === e.id);
			if (index === -1) {
				//3  不存在 第一次添加
				this.GoodsInfonew.goodsName = e.goodsName;
				this.GoodsInfonew.price = e.price;
				this.GoodsInfonew.picturepath = e.picturepath;
				this.GoodsInfonew.number = 1;
				this.GoodsInfonew.id = e.id;
				cart.push(this.GoodsInfonew);
			} else {
				// 4 已经存在购物车数据 执行 num++
				cart[index].number++;
			}
			// 5 把购物车重新添加回缓存中
			uni.setStorageSync("cart", cart);
			// 6 弹窗提示
			uni.showToast({
				title: '加入成功',
				icon: 'success',
				// true 防止用户 手抖 疯狂点击按钮 
				mask: true
			});
		},
		XQ: function(e) {
			uni.navigateTo({
				url: '../ProductDetails/ProductDetails?id=' + e
			})
		},
		change: function(e) {
			console.log(e);
			uni.showToast({
				title: "编号" + e,
				icon: "none"
			});
			uni.request({
				url: 'http://localhost:8080/commodity/selectCom?ccategoryid=' + e,
				method: 'POST',
				data: {},
				success: res => {
					this.goodsList = res.data.message;
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
